.add-option-error{
    color: $off-white;
    font-style: italic;
    padding: 0 $m-size;
    margin-top: $m-size 0 0 0;
}
.add-option{
    display: flex;
    padding: $m-size;
    flex-direction: column;
    padding: $m-size;
}
.add-option__input{
    background: $dark-blue;
    border: none;
   
    border-bottom: .3rem solid darken($color: $dark-blue, $amount: 10%);
    flex-grow: 1;
    margin: 0 0 $s-size 0;
    text-align: center;
    color: $off-white;
    padding: $s-size;
}
@media (min-width: $desktop-breakpoint){
   .add-option{
       flex-direction: row;
   }
   .add-option__input{
    margin-right: $s-size;
   }
}